* {
  box-sizing: border-box;
}

html {
  --bg: #f4f4f4;
  --fg: #25282a;
  --gray: #888;
  --royal: #0072ce;
  --blue: #6ad1e3;
  --primary: #93b0df;
  --secondary: var(--royal);
  --tertiary: #ff585d;
  background-color: var(--bg);
  color: var(--fg);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
  font-size: calc(.65em + .7vw);
  line-height: 1.3;
}

html ::selection {
  background: var(--secondary);
  color: var(--bg);
}

@media (prefers-color-scheme: dark) {
  html {
    --bg: #25282a;
    --fg: #fff;
    --secondary: var(--blue);
  }

  html ::selection {
    background: var(--secondary);
    color: var(--fg);
  }
}

body {
  max-width: 40em;
  margin: 0 auto;
  padding: 0 1em;
}

fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

.highlight {
  align-items: center;
  width: 100%;
  margin-bottom: .5em;
  display: flex;
}

.highlight label {
  margin-right: .5em;
}

.highlight input {
  caret-color: var(--secondary);
}

label {
  color: var(--gray);
}

button, .highlight {
  border-color: var(--primary);
  border: .1em solid var(--primary);
  border-radius: 5px;
  padding: .5em 1em;
}

button:hover, button:focus, button:focus-within, .highlight:hover, .highlight:focus, .highlight:focus-within {
  border-color: var(--secondary);
}

input {
  border: none;
  flex: 1;
}

input:read-only {
  color: var(--primary);
}

input[type="number"] {
  text-align: center;
  border-bottom: .1em solid var(--primary);
  width: 4em;
  margin: 0 1em;
  padding-left: .5em;
}

input[type="number"]:hover, input[type="number"]:focus {
  border-color: var(--secondary);
}

button, input {
  color: inherit;
  cursor: pointer;
  font: inherit;
  background: none;
  outline: none;
}

button {
  background: var(--primary);
  transition: top 50ms;
  position: relative;
}

button:hover, button:focus {
  top: -1px;
}

button:active {
  background: var(--secondary);
  border-color: var(--secondary);
  top: 1px;
}

.is-premium {
  border-left: .25em solid var(--secondary);
  margin-left: -.5em;
  padding-left: .25em;
}

table button {
  margin-left: 1rem;
}
